import React, { useState } from "react";
import { GoogleMap, Marker, InfoWindow } from "@react-google-maps/api";
import useInnerWidth from "../../hooks/useInnerWidth";
import { useEffect } from "react/cjs/react.development";

const center = {
  lat: 59.92723492128399,
  lng: 30.345408047401552,
};

const addresses = [
  {
    key: 1,
    position: { lng: 30.166975305008375, lat: 59.82693810998269 },
    description: "Dobrovoltsev St., 58-a, Krasnoselskiy dist.",
    open: "11:00 - 19:00",
  },
  {
    key: 2,
    position: { lng: 30.280342131630597, lat: 59.89621778909477 },
    description: "Shevtsova St., 6, Admiralteyskiy dist.",
    open: "10:00 - 19:00",
  },
  {
    key: 3,
    position: { lng: 30.492007835627124, lat: 59.84051425782886 },
    description: "Shlisselburgsky pr., 7, Kolpinskiy dist.",
    open: "11:00 - 21:00",
  },
  {
    key: 4,
    position: { lng: 30.303629187583883, lat: 59.95905278209715 },
    description: "Bolsh. Pushkarskays St., 28/2, Petrogradskiy dist.",
    open: "11:00 - 23:00",
  },
  {
    key: 5,
    position: { lng: 30.211767075758146, lat: 60.00764227529975 },
    description: "Turistskaya St., 34, Primoskiy dist.",
    open: "11:00 - 20:00",
  },
];

function Map(props) {
  const [currentWidth] = useInnerWidth();
  const [infoWindow, setInfoWindow] = useState();
  const [zoom, setZoom] = useState(10);

  const containerStyle = {
    width:
      currentWidth < props.maxWidth ? props.mobileWidth : props.desktopWidth,
    height: props.height,
  };

  const createInfoWindow = (address) => {
    setInfoWindow(address);
    setZoom(13);
  };

  const closeInfoWindow = () => {
    setInfoWindow();
    setZoom(10);
  };

  useEffect(() => {
    props.address && setInfoWindow(props.address);
    props.address && setZoom(13);
  }, [setInfoWindow, setZoom, props.address]);

  return (
    <GoogleMap mapContainerStyle={containerStyle} center={center} zoom={zoom}>
      <>
        {addresses.map((address) => {
          return (
            <Marker
              onClick={() => createInfoWindow(address)}
              key={address.key}
              position={address.position}
            />
          );
        })}{" "}
        {/* {infoWindow && (
          <InfoWindow
            position={infoWindow.position}
            onCloseClick={() => closeInfoWindow()}
          >
            <div>
              {infoWindow.description}
              <br />
              Open: {infoWindow.open}
            </div>
          </InfoWindow>
        )} */}
      </>
    </GoogleMap>
  );
}

export default React.memo(Map);
