import Button from "../../Common/Button";
import classes from "./Item.module.css";
import cartIcon from "../../../SVG/shopping-cart.svg";

function Item(props) {
  return (
    <div className={classes.item}>
      <h3>{props.name}</h3>
      <p>{props.description}</p>
      {!props.addedToCart && <Button onClick={props.onAddToCart}>Order Now</Button>}
      {props.addedToCart && (
        <div className={classes.actions}>
          <Button
            className={classes["order-button"]}
            onClick={props.onIncreaseAmount}
          >
            Add More
          </Button>
          <Button
            className={classes["cart-button"]}
            onClick={props.onOpenCart}
          >
            {props.amount && (
              <div className={classes["amount-status"]}>{props.amount}</div>
            )}
            <img
              className={classes["cart-icon"]}
              src={cartIcon}
              alt="Cart Icon"
            ></img>
          </Button>
        </div>
      )}
    </div>
  );
}

export default Item;
