import { useState } from "react";

import Map from "../Common/Map";
import line from "../../SVG/line.svg";
import classes from "./Footer.module.css";

const addresses = [
  {
    key: 1,
    position: { lng: 30.166975305008375, lat: 59.82693810998269 },
    description: "Dobrovoltsev St., 58-a, Krasnoselskiy dist.",
    open: "11:00 - 19:00",
  },
  {
    key: 2,
    position: { lng: 30.280342131630597, lat: 59.89621778909477 },
    description: "Shevtsova St., 6, Admiralteyskiy dist.",
    open: "10:00 - 19:00",
  },
  {
    key: 3,
    position: { lng: 30.492007835627124, lat: 59.84051425782886 },
    description: "Shlisselburgsky pr., 7, Kolpinskiy dist.",
    open: "11:00 - 21:00",
  },
  {
    key: 4,
    position: { lng: 30.303629187583883, lat: 59.95905278209715 },
    description: "Bolsh. Pushkarskays St., 28/2, Petrogradskiy dist.",
    open: "11:00 - 23:00",
  },
  {
    key: 5,
    position: { lng: 30.211767075758146, lat: 60.00764227529975 },
    description: "Turistskaya St., 34, Primoskiy dist.",
    open: "11:00 - 20:00",
  },
];

function Footer() {
  const [selectedAddress, setSelectedAddress] = useState();

  const addressHandler = (event) => {
    setSelectedAddress(
      addresses.find((address) => address.key === +event.target.id)
    );
  };

  return (
    <footer>
      <div id="Contacts" className={classes.contacts}>
        <div className={classes.map}>
          <p>Contacts</p>
          <Map
            height={"350px"}
            desktopWidth={"650px"}
            mobileWidth={"95vw"}
            maxWidth={1300}
            address={selectedAddress}
          ></Map>
        </div>
        <div className={classes.line}>
          <img src={line} alt="Pointer line" />
        </div>
        <div className={classes.address}>
          <h3>Our Addresses</h3>
          {addresses.map((address) => {
            return (
              <p id={address.key} key={address.key} onClick={addressHandler}>
                 {address.description}
              </p>
            );
          })}
        </div>
      </div>
      <div className={classes.copyright}>
        <a
          href="https://www.figma.com/community/file/997834539634615226"
          alt="copyright"
        >
          <p>2020 (c) - Samwyle bandtitos</p>
        </a>
      </div>
    </footer>
  );
}

export default Footer;
