import useInnerWidth from "../../hooks/useInnerWidth";
import { observer } from "mobx-react-lite";

import classes from "./Header.module.css";
import Button from "../Common/Button";
import logo from "../../Images/flowerLogo.png";
import logoText from "../../Images/samwayleLogo.png";
import menuButton from "../../SVG/menuButton.svg";
import cartIcon from "../../SVG/shopping-cart.svg";
import cartIconBlack from "../../SVG/shoppingCartBlack.svg";
import store from "../../Store/store";

const Header = observer((props) => {
  const [currentWidth] = useInnerWidth();

  return (
    <header className={classes.header}>
      <div className={classes.image}>
        <img className={classes.logo} src={logo} alt="Logo" />
        <img className={classes.text} src={logoText} alt="Logo Text" />
      </div>
      {currentWidth <= "992" ? (
        <>
          <button className={classes["burger-menu"]} onClick={props.onOpenMenu}>
            <img src={menuButton} alt="Menu Button" />
          </button>
          {store.cartItems.length > 0 && (
            <>
              <button
                className={classes["cart-menu"]}
                onClick={props.onOpenCart}
              >
                <img
                  className={classes["cart-menu-icon"]}
                  src={cartIconBlack}
                  alt="Cart Button"
                />
              </button>
              <div className={classes["mobile-amount-status"]}>
                {store.cartTotalAmount}
              </div>
            </>
          )}
        </>
      ) : (
        <nav className={classes.nav}>
          <p onClick={props.onNavMenu}>About us</p>
          <p onClick={props.onNavMenu}>Promotion</p>
          <p onClick={props.onNavMenu}>Shop</p>
          <p onClick={props.onNavMenu}>Contacts</p>
          <Button
            onClick={props.onOpenFeedbackModal}
            className={classes.button}
          >
            Contact Us
          </Button>
          <Button className={classes["cart-button"]} onClick={props.onOpenCart}>
            <div className={classes["cart-title"]}>
              Cart
              <img
                className={classes["cart-icon"]}
                src={cartIcon}
                alt="Cart Icon"
              ></img>
            </div>
            <div className={classes["amount-status"]}>
              {store.cartTotalAmount}
            </div>
          </Button>
        </nav>
      )}
    </header>
  );
});

export default Header;
