import { useState } from "react";
import { observer } from "mobx-react-lite";
import store from "../../Store/store";

import Modal from "../Common/Modal";
import Button from "../Common/Button";
import CartItem from "./CartItem";
import OrderForm from "./OrderForm";
import classes from "./Cart.module.css";

const Cart = observer((props) => {
  const [openedOrderForm, setOpenedOrderForm] = useState(false);

  const orderHandler = () => {
    setOpenedOrderForm((prev) => !prev);
  };

  return (
    <Modal classNameModal={classes.modal} onClick={props.onCloseCart}>
      <div className={classes["cart"]}>
        {store.cartItems.length === 0 ? (
          <h3 className={classes["cart-empty"]}>Cart is empty!</h3>
        ) : (
          <>
            <div className={classes["cart-items"]}>
              {store.cartItems.map((item) => (
                <CartItem
                  key={item.id}
                  title={item.title}
                  amount={item.amount}
                  price={item.price}
                  onIncrease={() => store.increaseAmount(item.id)}
                  onDecrease={() => store.decreaseAmount(item.id)}
                  onChangeInput={(value) => store.changeInput(value, item.id)}
                />
              ))}
            </div>
            <p>Total price: ${store.cartTotalPrice.toFixed(2)}</p>
            <div className={classes.order}>
              <OrderForm style={openedOrderForm ? null : { display: "none" }} />
              <Button className={classes.button} onClick={orderHandler}>
                {openedOrderForm ? "Back" : "Next"}
              </Button>
            </div>
          </>
        )}
      </div>
    </Modal>
  );
});

export default Cart;
