import classes from "./CartItem.module.css";

function CartItem(props) {
  const changeInputHandler = (event) => {
    props.onChangeInput(event.target.value);
  };

  return (
    <div className={classes["cart-item"]}>
      <div className={classes.title}>
        <h3>{props.title}</h3>
        <p>Price: ${(props.amount * props.price).toFixed(2)}</p>
      </div>
      <div className={classes.amount}>
        <button onClick={props.onIncrease} className={classes["button-right"]}>
          +
        </button>
        <input onChange={changeInputHandler} value={props.amount} />
        <button onClick={props.onDecrease} className={classes["button-left"]}>
          -
        </button>
      </div>
    </div>
  );
}

export default CartItem;
