import { makeAutoObservable } from "mobx";

class Store {
  feedbackOpened = false;
  cartOpened = false;
  cartItems = [];

  constructor() {
    makeAutoObservable(this);
  }

  increaseAmount(id) {
    this.cartItems.find((item) => item.id === id).amount++;
  }

  addNewItemToCart(id, title, price) {
    this.cartItems = [
      { id: id, title: title, amount: 1, price: price },
      ...this.cartItems,
    ];
  }

  decreaseAmount(id) {
    let targetItem = this.cartItems.find((item) => item.id === id);
    if (targetItem.amount > 1) {
      targetItem.amount--;
    } else {
      this.cartItems = this.cartItems.filter((item) => item.id !== id);
    }
  }

  isAddedToCart(id) {
    let targetItem = this.cartItems.find((item) => item.id === id);
    if (targetItem) {
      return true;
    } else {
      return false;
    }
  }

  itemAmount(id) {
    return this.cartItems.find((item) => item.id === id)?.amount;
  }

  get cartTotalPrice() {
    return this.cartItems.reduce(
      (sum, current) => sum + current.price * current.amount,
      0
    );
  }

  get cartTotalAmount() {
    return this.cartItems.reduce((sum, current) => sum + current.amount, 0);
  }

  changeInput(value, id) {
    this.cartItems.find((item) => item.id === id).amount = +value;
  }

  closeCart() {
    this.cartOpened = false;
  }

  openCart() {
    this.cartOpened = true;
  }

  closeFeedbackModal() {
    this.feedbackOpened = false;
  }

  openFeedbackModal() {
    this.feedbackOpened = true;
  }
}

export default new Store();
