import { observer } from "mobx-react-lite";
import WidthWrapper from "./Components/Common/WidthWrapper";

import store from "./Store/store";
import burgerMenuStore from "./Store/burgerMenuStore";

import Cart from "./Components/Cart/Cart";
import Feedback from "./Components/Feedback/Feedback";
import NavigationModal from "./Components/UI/MobileBurgerMenuModal";
import Header from "./Components/Main/Header";
import Promotion from "./Components/Main/Promotion";
import CoffeeShop from "./Components/Main/CoffeeShop";
import Description from "./Components/Main/Description";
import ItemList from "./Components/Main/ItemList/ItemList";
import Footer from "./Components/Main/Footer";
import { LoadScript } from "@react-google-maps/api";

const App = observer(() => {
  return (
    <>
      {burgerMenuStore.menuOpened && (
        <NavigationModal
          onCloseMenu={() => burgerMenuStore.closeBurgerMenu()}
          onMobileNavMenu={(clickedLink) =>
            burgerMenuStore.navigateMenu(clickedLink)
          }
        />
      )}
      {store.feedbackOpened && (
        <Feedback onCloseFeedbackModal={() => store.closeFeedbackModal()} />
      )}
      <WidthWrapper>
        <Header
          onOpenMenu={() => burgerMenuStore.openBurgerMenu()}
          onNavMenu={(clickedLink) => burgerMenuStore.navigateMenu(clickedLink)}
          onOpenFeedbackModal={() => store.openFeedbackModal()}
          onOpenCart={() => store.openCart()}
        />
        <CoffeeShop
          onOrderHere={(clickedLink) =>
            burgerMenuStore.navigateMenu(clickedLink)
          }
        />
        <Description />
        <Promotion
          onOrderNow={(clickedLink) =>
            burgerMenuStore.navigateMenu(clickedLink)
          }
        />
      </WidthWrapper>
      <ItemList />
      <LoadScript googleMapsApiKey="AIzaSyAhamM8NY8xsXj-ss0G67UV2h60w4Rt8-g">
        <WidthWrapper>
          <Footer />
        </WidthWrapper>
        {store.cartOpened && <Cart onCloseCart={() => store.closeCart()} />}
      </LoadScript>
    </>
  );
});

export default App;
