import { useState } from "react";

import Button from "../Common/Button";
import classes from "./OrderForm.module.css";
import Map from "../Common/Map";

const addresses = [
  {
    key: 1,
    position: { lng: 30.166975305008375, lat: 59.82693810998269 },
    description: "Dobrovoltsev St., 58-a, Krasnoselskiy dist.",
    open: "11:00 - 19:00",
  },
  {
    key: 2,
    position: { lng: 30.280342131630597, lat: 59.89621778909477 },
    description: "Shevtsova St., 6, Admiralteyskiy dist.",
    open: "10:00 - 19:00",
  },
  {
    key: 3,
    position: { lng: 30.492007835627124, lat: 59.84051425782886 },
    description: "Shlisselburgsky pr., 7, Kolpinskiy dist.",
    open: "11:00 - 21:00",
  },
  {
    key: 4,
    position: { lng: 30.303629187583883, lat: 59.95905278209715 },
    description: "Bolsh. Pushkarskays St., 28/2, Petrogradskiy dist.",
    open: "11:00 - 23:00",
  },
  {
    key: 5,
    position: { lng: 30.211767075758146, lat: 60.00764227529975 },
    description: "Turistskaya St., 34, Primoskiy dist.",
    open: "11:00 - 20:00",
  },
];

function OrderForm(props) {
  const [recievingMethod, setRecievingMethod] = useState("Empty");
  const [enteredName, setEnteredName] = useState("Empty");
  const [enteredPhone, setEnteredPhone] = useState("Empty");
  const [enteredComment, setEnteredComment] = useState("Empty");
  const [enteredAddress, setEnteredAddress] = useState("Empty");
  const [enteredPayment, setEnteredPayment] = useState("Empty");

  const takeawayHandler = () => {
    setRecievingMethod("Takeaway");
    setEnteredAddress("Empty");
    setEnteredPayment("Empty");
  };

  const deliveryHandler = () => {
    setRecievingMethod("Delivery");
    setEnteredAddress("Empty");
    setEnteredPayment("Empty");
  };

  const nameBlurHandler = (event) => {
    setEnteredName(event.target.value);
  };

  const phoneBlurHandler = (event) => {
    setEnteredPhone(event.target.value);
  };

  const commentBlurHandler = (event) => {
    setEnteredComment(event.target.value);
  };

  const orderSubmitHandler = (event) => {
    event.preventDefault();
    console.log({
      name: enteredName,
      phone: enteredPhone,
      comment: enteredComment,
      recieving: {
        recievingMethod: recievingMethod,
        address: enteredAddress,
        payment: enteredPayment,
      },
    });
  };

  const map =
    recievingMethod === "Takeaway" ? (
      <Map
        height={"250px"}
        desktopWidth={"100%"}
        mobileWidth={"100%"}
        maxWidth={992}
        address={addresses.find(
          (address) => address.description === enteredAddress
        )}
      />
    ) : null;

  const paymentHandler = (event) => {
    setEnteredPayment(event.target.id);
  };

  const addressHandler = (event) => {
    setEnteredAddress(event.target.value);
  };

  const deliveryForm = (
    <div>
      <div className={classes.address} onBlur={addressHandler}>
        <label htmlFor="address">Address</label>
        <input id="address" />
      </div>
      <div>
        <input onClick={paymentHandler} id="cash" type="radio" name="payment" />
        <label htmlFor="cash">Cash</label>
        <input
          onClick={paymentHandler}
          id="creditCard"
          type="radio"
          name="payment"
        />
        <label htmlFor="creditCard">Credit Card</label>
      </div>
    </div>
  );

  const selectedAddressHandler = (event) => {
    setEnteredAddress(event.currentTarget.textContent);
  };

  const takeawayForm = (
    <div>
      {addresses.map((address) => (
        <div key={address.key} onClick={selectedAddressHandler}>
          <input type="radio" id={address.key} name="address" />
          <label htmlFor={address.key}>{address.description}</label>
        </div>
      ))}
    </div>
  );

  return (
    <form onSubmit={orderSubmitHandler} style={props.style}>
      <div className={classes["order-form-container"]}>
        <div className={classes["order-info"]}>
          <label>Name</label>
          <input onBlur={nameBlurHandler} />
          <label>Phone</label>
          <input onBlur={phoneBlurHandler} />
          <label>Comment</label>
          <textarea onBlur={commentBlurHandler}></textarea>
        </div>
        <div className={classes["order-recieving"]}>
          <div>
            <input
              onChange={deliveryHandler}
              id="delivery"
              type="radio"
              name="method"
            />
            <label htmlFor="delivery">Delivery</label>
          </div>
          {recievingMethod === "Delivery" && deliveryForm}
        </div>
        <div>
          <div>
            <input
              onChange={takeawayHandler}
              id="takeaway"
              type="radio"
              name="method"
            />
            <label htmlFor="takeaway">Takeaway</label>
          </div>
          {recievingMethod === "Takeaway" && takeawayForm}
        </div>
      </div>
      {map}
      <Button className={classes["order-button"]} type="submit">
        Order
      </Button>
    </form>
  );
}

export default OrderForm;
